import { connect } from "react-redux"
import Posts from "./Posts"

const mapStateToProps = (state, ownProps) => {
   const content = state.main.content;
   const UI = state.main.UI;
   const posts = content.profile.posts;
   // debugger
   //      
   const getTime = time => new Date(time).getTime();
   const sortPosts = posts.slice(0).sort((a, b) => {
      if (getTime(a.date) > getTime(b.date)) {
         return 1;
      }
      if (getTime(a.date) == getTime(b.date)) {
         return 0;
      }
      if (getTime(a.date) < getTime(b.date)) {
         return -1;
      }
   });

   return {
      posts: sortPosts,
      content: {
         user: content.profile.user,
         posts: content.profile.posts,
      },
      UI: {
         buttonsImages: UI.profile.posts.postButtonsImg,
      }
   }
}

export default connect(mapStateToProps, null)(Posts)