// * messages
export const addNewMessageActionCreator = number => (
   {
      type: "ADD-NEW-MESSAGE",
      interlocutor: "",
      number: number,
   }
)

export const changeDialogsSearchTextActionCreator = text => (
   {
      type: "CHANGE-DIALOGS-SEARCH-TEXT",
      text: text,
   }
)

export const changeDialogTextActionCreator = text => (
   {
      type: "CHANGE-DIALOG-TEXT",
      text: text,
   }
)

// * profile
export const addNewPostActionCreator = () => (
   {
      type: "ADD-NEW-POST",
   }
)

export const changeNewPostTextActionCreator = text => (
   {
      type: "CHANGE-NEW-POST-TEXT",
      currentText: text,
   }
)

export const changeCommunityStatus = (status, community) => (
   {
      type: "CHANGE-COMMUNITY-STATUS",
      status: status,
      community: community,
   }
)

export const changeMessageSearchStatusActionCreator = () => (
   {
      type: "CHANGE-MESSAGE-SEARCH-STATUS",
   }
)

export const saveSoughtMessageActionCreator = soughtMessage => (
   {
      type: "SAVE-SOUGHT-MESSAGE",
      soughtMessage: soughtMessage
   }
)