import React from 'react'
import { NavLink } from 'react-router-dom'
import setSidebarPositionAndSize from '../../../../lib/setSidebarPosition/setSidebarPositionAndSize';
import s from './DialogsItem.module.css'

// const DialogsItem = ({content}) => (
// )
class DialogsItem extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      if (this.props.content.dialogNumber === this.props.content.lastNumber) setSidebarPositionAndSize("fixed", "Messages");
   }

   render() {
      return (
         <NavLink to={`/Messages/${this.props.content.dialogId}`}>
            <div
               className={`${s.dialogsItem} messages__dialogs-item`}
               onClick={
                  () => {
                     if (!this.props.content.searchStatus) return;
                     this.props.saveSoughtMessage({
                        dialogId: this.props.content.dialogId,
                        message: {
                           date: this.props.content.dateStr,
                           messageText: this.props.content.fullMessage,
                        },
                     })
                  }
               }
            >
               <div className={`${s.personAvatar} messages__person-avatar`}>
                  <img src={this.props.content.image} alt="person avatar" />
               </div>
               <div className={s.personName}>
                  {this.props.content.name}
               </div>
               <div className={s.personMessage}>
                  {this.props.content.message}
               </div>
               <div className={s.messageDate}>
                  {this.props.content.date}
               </div>
            </div>
         </NavLink>
      )
   }
}

export default DialogsItem