import { fromJS, Map } from "immutable";
import { connect } from "react-redux";
import getFormattedDate from "../../../../lib/createCivilDate/getFormattedDate";
import editAndCheckSearchText from "../../../../lib/editAndCheckSearchText";
import messageShrink from "../../../../lib/messageShrink";
import Dialogs from "./Dialogs";


const mapStateToProps = (state, ownProps) => {
   const content = state.main.content;
   //      
   const getTime = time => new Date(time).getTime();


   //                      !
   // *  
   const dialogsSearchText = editAndCheckSearchText(state.main.content.messenger.searchText)
   let stateMap = fromJS(state.main);
   let newState = stateMap.toJS();

   let allDialogs = newState.content.messenger.dialogs;
   let searchMessages = [];

   //  ,    
   let searchDialogs = allDialogs.filter(dialog => {
      return Boolean(dialog.messages.find(message => (
         editAndCheckSearchText(message.messageText).toLowerCase().includes(dialogsSearchText)
      )
      ))
   })

   //   .     ,
   //        
   searchDialogs.forEach((dialog) => {
      dialog.messages.forEach((message) => {
         if (editAndCheckSearchText(message.messageText)
            .toLowerCase()
            .includes(dialogsSearchText)) {
            let messageObj = { ...message };
            let dialogObj = {
               ...dialog,
               messages: [messageObj],
            };
            searchMessages.push(dialogObj);
         }
      })
   });

   if (searchMessages.length === 0) {
      searchMessages = ["No matching messages found."];
   }

   //                      !

   let dialogs = content.messenger.dialogs.slice(0).sort((a, b) => {
      const aLength = a.messages.length
      const bLength = b.messages.length
      if (getTime(a.messages[aLength - 1].date) > getTime(b.messages[bLength - 1].date)) {
         return 1;
      }
      if (getTime(a.messages[aLength - 1].date) == getTime(b.messages[bLength - 1].date)) {
         return 0;
      }
      if (getTime(a.messages[aLength - 1].date) < getTime(b.messages[bLength - 1].date)) {
         return -1;
      }
   });

   console.log(content.messenger.searchStatus)

   return {
      dialogs: dialogsSearchText.length ? searchMessages : dialogs,
      users: content.users,
      searchText: dialogsSearchText,
      searchStatus: content.messenger.searchStatus,
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      functions: {
         getFormattedDate: (dateStr, page) => {
            return getFormattedDate(dateStr, page);
         },
         messageShrink: (messageText) => {
            return messageShrink(messageText);
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dialogs);