import React from "react";
import s from "./Dialogs.module.css"
import DialogsItemContainer from "./DialogsItemContainer";
import NoMatchingMessagesFound from "./NoMatchingMessagesFound";
import SendFirstMessage from "./SendFirstMessage";

// const Dialogs = ({ searchText, dialogs, users, this.props.functions }) => {
class Dialogs extends React.Component {

   constructor(props) {
      super(props);
   }

   componentDidMount() {
      // this.props.functions.changeSearchStatus(this.props.searchText)
   }

   render() {
      if (!this.props.dialogs.length) return (
         <SendFirstMessage />
      )

      if (this.props.dialogs.length === 1 && this.props.dialogs[0] === "No matching messages found.") return (
         <NoMatchingMessagesFound />
      )

      return (
         <div className={s.dialogs}>
            {this.props.dialogs.slice(0).reverse().map((dialog, i) => {
               const user = this.props.users.find((usersItem) => usersItem.name == dialog.name);
               const dateStr = dialog.messages[dialog.messages.length - 1].date

               return (
                  <DialogsItemContainer
                     key={i}
                     content={
                        {
                           searchStatus: this.props.searchStatus,
                           dialogNumber: i,
                           lastNumber: this.props.dialogs.length - 1,
                           image: user.avatar,
                           name: user.name,
                           fullMessage: dialog.messages[dialog.messages.length - 1].messageText,
                           message: this.props.functions.messageShrink(dialog.messages[dialog.messages.length - 1].messageText),
                           date: this.props.functions.getFormattedDate(dateStr, "dialog"),
                           dateStr: dateStr,
                           dialogId: dialog.dialogId,
                        }
                     }
                     soughtMessage={
                        {
                           dialogId: "",
                           message: {date: "", messageText: "" }
                        }
                     }
                  />
               )
            })
            }
         </div>
      )
   }


}

export default Dialogs