import { connect } from "react-redux"
import editAndCheckSearchText from "../../../../../lib/editAndCheckSearchText";
import { changeDialogsSearchTextActionCreator, changeMessageSearchStatusActionCreator, dialogsSearchActionCreator } from "../../../../../redux/actionCreators/mainActionCreators";
import MessagesSearch from "./MessagesSearch"

const mapStateToProps = (state, ownProps) => {
   const searchUI = state.main.UI.messages.messagesSearch;
   // console.log(state.main.content.messenger.searchStatus)
   return {
      searchIcon: searchUI.searchIcon,
      groupChatIcon: searchUI.groupChatIcon,
      otherIcon: searchUI.otherIcon,
      text: state.main.content.messenger.searchText
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      changeDialogsSearchText: text => {
         let action = changeDialogsSearchTextActionCreator(text);
         dispatch(action);
      },
      changeSearchStatus: () => {
         let action = changeMessageSearchStatusActionCreator()
         dispatch(action);
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessagesSearch);