import { connect } from "react-redux"
import scrollForSoughtMessageAndRemoveSoughtClass from "../../../lib/scrollForSoughtMessageAndRemoveSoughtClass"
import ChatMessage from "./ChatMessage"

const mapStateToProps = (state, ownProps) => {
   return {
      key:ownProps.key,
      messageState:ownProps.messageState,
      messenger: state.main.content.messenger,
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      setStyleForSoughtMessage: () => {
         scrollForSoughtMessageAndRemoveSoughtClass();
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatMessage)